import { Component, Input } from '@angular/core';
import { IViewComponentDropPredicateData, ViewDesignerFacade } from '@helix/platform/view/designer';
import { RxShellDesignModel } from './shell-design.model';
import { RX_SHELL } from '@helix/platform/view/api';

@Component({
  selector: 'rx-shell-design',
  templateUrl: './shell-design.component.html',
  styleUrls: ['./shell-design.component.scss']
})
export class RxShellDesignComponent {
  @Input()
  model: RxShellDesignModel;

  constructor(public viewDesignerFacade: ViewDesignerFacade) {}

  dropPredicate(data: IViewComponentDropPredicateData): boolean {
    return data.draggedViewComponentDescriptor.type !== RX_SHELL.navBar.action;
  }

  dropPredicateAction(data: IViewComponentDropPredicateData): boolean {
    return data.draggedViewComponentDescriptor.type === RX_SHELL.navBar.action;
  }
}
